// import { HeaderLinkAction } from "stores/HeaderLink/HeaderLinkStore";

import Web3 from "web3";
import Onboard from "bnc-onboard";

import CONFIG from "../../config";

let provider;
const networkId = CONFIG.NETWORK_ID;
const onboardApiKey = CONFIG.ONBOARD_API_KEY;
const wallets = CONFIG.WALLETS;
const contractAddress = CONFIG.CONTRACT_ADDRESS;
const abi = CONFIG.ABI;

let setAuthen;
let setAuthenMsg;

const onboard = Onboard({
  dappId: onboardApiKey, // [String] The API key created by step one above
  networkId: networkId, // [Integer] The Ethereum network ID your Dapp uses.
  darkMode: true,
  subscriptions: {
    wallet: (wallet) => {
      provider = wallet.provider;
    },
    address: () => {
      const oldAccount = localStorage.getItem("userAddress");

      if (oldAccount) {
        onDisconnect();
      }
    },
    network: () => {
      const oldAccount = localStorage.getItem("userAddress");

      if (oldAccount) {
        onDisconnect();
      }
    },
  },
  walletSelect: {
    wallets: wallets,
  },
});

const init = async (authenFunc) => {
  try {
    setAuthen = authenFunc.setAuthen;
    setAuthenMsg = authenFunc.setAuthenMsg;
  } catch (error) {
    // console.error(error);
  }
};

const onDisconnect = async () => {
  await onboard.walletReset();
  localStorage.setItem("userAddress", "");
  setAuthen(false);
  setAuthenMsg("");
};

const onConnect = async () => {
  try {
    let result = false;

    const walletSelected = await onboard.walletSelect();
    const readyToTransact = await onboard.walletCheck();

    if (walletSelected && readyToTransact) {
      const currentState = await onboard.getState();
      const userAddress = currentState.address;
      const contract = new new Web3(new Web3(provider)).eth.Contract(
        abi,
        contractAddress
      );
      const balance = await contract.methods.balanceOf(userAddress).call();
      if (balance > 0) {
        setAuthen(true);
        setAuthenMsg("Verify success.");
        result = true;
      } else {
        setAuthen(false);
        setAuthenMsg(
          "Verify error. You don't have a PIG. The software allows only the PIG holder."
        );
      }
      localStorage.setItem("userAddress", userAddress);
    }

    return result;
  } catch (error) {
    // console.error(error);
  }
};

export default {
  init,
  onConnect,
  onDisconnect,
};
