import { createSlice } from "@reduxjs/toolkit";
// import HeaderLinkService from "../../services/HeaderLink/HeaderLinkService";

const myState = {
  userAddress: null,
  userBalance: 0,
};

const myStore = createSlice({
  name: "HeaderLinkStore",
  initialState: myState,
  reducers: {
    setUserBalance: (state, { payload }) => {
      try {
        state.userBalance = payload.userBalance;
      } catch {
        // console.log("error");
      }
    },
    setUserAddress: (state, { payload }) => {
      try {
        state.userAddress = payload.userAddress;
      } catch {
        // console.log("error");
      }
    },
  },
});

export const HeaderLinkReducer = myStore.reducer;
export const HeaderLinkAction = myStore.actions;
